.apartmentList {
  padding: 5rem 0;
  margin-top: -40px;
  /* width: 100%; */
  display: flex;
  align-items: center;
}
.apartmentList-center {
  /* width: 80vw; */
  margin: 0 auto;
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  grid-row-gap: 2rem;
  grid-column-gap: 4rem;
  object-fit: cover;
  padding-left: 6rem;
  padding-right: 6rem;
  justify-content: center;
}
@media only screen and (min-width: 656px) {
  .apartmentList-center {
    width: 100%;
    margin: 0 0;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    grid-row-gap: 2rem;
    grid-column-gap: 4rem;
    object-fit: cover;
    padding-left: 2.5rem;
    padding-right: 2.5rem;
    justify-content: center;
  }
}
@media only screen and (min-width: 514px) and (max-width: 655px) {
  .apartmentList-center {
    width: 100%;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin-left: 5rem;
    margin: 4.5rem;
    margin-top: 0;
    margin-bottom: 0;
  }
}
@media screen and (max-width: 513px) {
  .apartmentList-center {
    width: 0;
    justify-content: center;
    align-items: center;
    text-align: center;
    margin-left: 5rem;
    /* margin: 4.5rem; */
    margin-top: 0;
    margin-bottom: 0;
  }
}
